.ingredient_control__button {
    box-sizing: border-box;
    opacity: 1;
    z-index: -1;
    position: absolute;
    width: 10vh;
    height: 10vh;
    margin: 5vh;
    background-color: #fff;
    color: #3dbacc;
    font-size: 3vh;
    font-weight: bold;
    border-radius: 50%;
    border: 4px solid #fff;
    cursor: pointer;
    transition: .2s;
    padding: 0;
    outline: none;

    @media (orientation: portrait) {
        width: 13vw;
        height: 13vw;
        margin: 3vw;
        font-size: 5vw;
        border: 2px solid #fff; }

    &:hover {
        background-color: #E7655C;
        color: #fff; }

    &:focus {
        background-color: #E7655C;
        color: #fff; }

    &--counter {
        top: 0;
        left: 14vw;
        @media (orientation: portrait) {
            top: 19vh;
            left: -2vw; } }

    &--rotate {
        top: 0;
        right: 17vw;
        @media (orientation: portrait) {
            top: 19vh;
            right: -2vw; } }

    &--shrink {
        bottom: 0;
        left: 14vw;
        @media (orientation: portrait) {
            bottom: 27vh;
            left: 0vw; } }

    &--enlarge {
        bottom: 0;
        right: 17vw;
        @media (orientation: portrait) {
            bottom: 27vh;
            right: 0vw; } }

    &--higher {
        top: 30vh;
        left: 11vw;
        @media (orientation: portrait) {
            top: 13vh;
            left: unset;
            right: 9vw; } }

    &--lower {
        bottom: 30vh;
        left: 11vw;
        @media (orientation: portrait) {
            bottom: unset;
            top: 13vh;
            left: 9vw; } } }
